body {
  margin: 0;
}
html {
  scroll-behavior: smooth;
}
.Home {
  display: flex;
  justify-content: space-between;
}
.Home-text {
  margin-top: 160px;
}
.title {
  color: #875020;
  width: 500px;
  text-wrap: unset;
  font-size: 30px;
}
.Text-Home {
  font-size: 24px;
}
.btn-perspectivs-div {
  display: flex;
  margin: 40px 0;
}
.img-home {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  height: 800px;
  width: 40%;
  background: #e9b38c;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-perspectiv {
  font-size: 18px;
  padding: 10px;
  background: #d79d65;
  border-radius: 5px;
  margin-right: 20px;
}
.btn {
  font-size: 18px;
  padding: 15px 50px;
  background: #cc9e71;
  color: #000000;
  text-decoration: none;
  border-radius: 10px;
  transition: all 0.33s;
  margin-right: 30px;
}
.avatarki-btnAbout {
  display: flex;
  width: 110%;
  justify-content: space-between;
}
.btn:hover {
  background: #c18952;
}
.avatat {
  display: flex;
  align-items: center;
  font-size: 12px;
}

.About {
  display: flex;
  margin-top: 250px;
}
.img-About {
  margin: 0;
  height: 100%;
  object-fit: cover;
  width: 50%;
}
.text-about {
  padding: 0px 50px;
  font-size: 30px;
}
.text-about p {
  padding: 30px 0;
  width: 80%;
}
.Servisec {
  font-size: 24px;
}
.Servisec-p {
  padding-bottom: 30px;
}
.Servisecs {
  display: flex;
  align-items: center;
  padding-bottom: 30px;
}
.Servisecs p {
  padding-left: 20px;
}
.circle {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 50%;
}
.Contact {
  font-size: 30px;
}
.email-phone-adress {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.email,
.phone,
.adress {
  padding: 15px 0px;
}

.img-map {
  width: 100%;
  height: 500px;
  background: white;
  align-items: center;
  justify-content: center;
  display: flex;
}

@media screen and (max-width: 1600px) {
  .text-about p {
    font-size: 20px;
  }
}

@media screen and (max-width: 1200px) {
  .avatarki-btnAbout {
    justify-content: flex-start;
    width: 100%;
  }
  .Home {
    justify-content: none;
  }
  .text-about p {
    width: 100%;
  }
  .img-map {
    height: 250px;
  }
  .img-home {
    position: static;
    background: initial;
    height: auto;
    width: auto;
    align-items: center;
    justify-content: center;
  }
  .BackPack-img {
    width: 400px;
    height: 400px;
  }
  .text-about {
    padding: 30px;
  }
  .img-About {
    display: none;
  }
  .email-phone-adress {
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
  }
}
@media screen and (max-width: 1050px) {
  .Home {
    flex-direction: column;
  }
  .center-home-text {
    display: flex;
    align-items: center;
  }
  .Home-text {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
  }
}
@media screen and (max-width: 650px) {
  .title {
    font-size: 18px;
    width: auto;
  }
  .btn {
    font-size: 12px;
    width: 125px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 400px) {
  .btn-perspectiv {
    font-size: 10px;
  }
  .avatarki-btnAbout {
    align-items: center;
  }
  .btn {
    font-size: 8px;
    padding: 15px 10px;
  }
  .BackPack-img {
    width: 200px;
  }
  .About {
    margin-top: 25px;
  }
  .text-about p {
    font-size: 16px;
  }
  .Servisec {
    font-size: inherit;
  }
  .Contact {
    font-size: inherit;
  }
  .Home-text {
    margin-top: 50px;
  }
}
