.center-div {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  margin-top: 50px;
}
.btn-leave-accaunt {
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  background: rgb(246, 106, 106);
  transition: all 0.33s;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Profile-Main {
  margin-top: 75px;
}
.Account {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.edit-pass {
  text-decoration: underline;
  cursor: pointer;
  color: rgb(120, 85, 223);
}

.btn-leave-accaunt:hover {
  background: rgb(243, 64, 64);
}
.Account_content {
  font-size: 25px;
  border-bottom: 1px solid #000000;
}

.font_size-30px {
  font-size: 30px;
  padding: 20px 0px;
  padding-top: 0;
}
.Settings_content {
  border-bottom: 1px solid black;
}
.border-bottom {
  text-decoration: underline;
  color: rgb(113, 113, 237);
  cursor: pointer;
}
.nav-acc_item {
  cursor: pointer;
  width: min-content;
  margin-top: 30px;
  color: #000000;
  transition: all 0.33s;
}
.nav-acc_item.active {
  text-decoration: underline;
}
.nav-acc_item:hover {
  text-decoration: underline;
}
.border-bottom {
  width: 150px;
}
.ModalTypeEditors {
  position: fixed;
  z-index: 100000000;
  left: 0;
  top: 0;
  background: #ded3d3;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.ModalTypeEditors.open {
  display: flex;
}
@media screen and (max-width: 400px) {
  .Account_content {
    font-size: 18px;
  }
}
