.Orders-buy {
  width: 100%;
  padding: 0px 20px;
  border-bottom: 1px solid black;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}
.left-Order {
  padding: 10px;
}
.full-Order {
  margin-top: 50px;
  display: flex;
}
.ResultPrice-Count {
  padding: 0px 30px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 45%;
}
.UserInfoOrder {
  width: 800px;
}
.bottom-Orders {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.UserInfoOrder {
  margin-top: 10px;
  padding: 10px 0;
  border-right: 1px solid black;
}
.inputs-wrap {
  display: flex;
  flex-wrap: wrap;
}
.input_3-4 {
  margin-left: 150px;
}
.input-fieldOrder {
  position: relative;
  width: 140%;
  margin: 0px 20px;
  margin-left: 0;
}

.input {
  margin-top: 15px;
  width: 100%;
  outline: none;
  border-radius: 8px;
  height: 45px;
  border: 1.5px solid #363636;
  background: transparent;
  padding-left: 10px;
}
.input:focus {
  border: 1.5px solid #de8b10;
}
.input-fieldOrder .labelOrder {
  position: absolute;
  top: 5px;
  font-size: 12px;
  left: 5px;
  color: #303030;
  background-color: #deb292;
  transition: all 0.3s ease;
  pointer-events: none;
  z-index: 1;
  padding-left: 5px;
  padding-right: 5px;
}
@media screen and (max-width: 1300px) {
  .input-fieldOrder {
    position: relative;
    margin: 0px 20px;
    margin-left: 0;
    width: auto;
  }
  .input_3-4 {
    margin-left: 150px;
  }
  .inputs-wrap {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
  .input_3-4 {
    margin-left: 0%;
  }
  .ResultPrice-Count {
    width: 100%;
    height: 100%;
    justify-content: initial;
  }
  .Orders-buy {
    border: 0;
  }
}
@media screen and (max-width: 900px) {
  .bottom-Orders {
    display: flex;
    flex-direction: column;
    width: min-content;
  }
  .UserInfoOrder {
    border: 0;
    border-bottom: 1px solid black;
    width: 100%;
  }
  .Orders-buy {
    padding: 0;
  }
}
