.Search-input {
  position: relative;
  border: 1px solid rgb(43, 43, 43);
  background: initial;
  border-radius: 10px;
  width: 200px;
  padding: 5px 50px;
  height: 30px;
}
.SearchImg {
  width: 30px;
  height: 30px;
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  margin-left: 11px;
  top: 133px;
}
.TopShopPage,
.filters {
  margin-top: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn-market p {
  padding: 0px 10px;
}
.ModelNeedRegister {
  position: fixed;
  z-index: 10000;
  top: 0%;
  right: 0%;
  width: 100%;
  height: 100%;
  transform: translateY(-100%);
  background: rgb(233, 229, 229);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transition: all 0.33s;
}
.ModelNeedRegister.open {
  transform: none;
}
.CloseRegisterModel {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 3%;
  cursor: pointer;
  right: 3%;
}
.btn-market {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  border-radius: 10px;
  background: #f58244;
  transition: all 0.33s;
}
.btn-market:hover {
  background: #e86924;
}
/* ОКОНЧАНИЯ СЕРЧА И БТН МАРКЕТА */

.Sort {
  display: flex;
  flex-direction: column;
}

.item_sort {
  list-style: none;
  color: black;
  cursor: pointer;
  transition: all 0.33s;
  padding: 10px 0;
}
.item_sort.active {
  color: #e4752a;
  list-style: circle;
}
.Category {
  display: flex;
}
.p-category {
  display: flex;
  flex-direction: column;
}
.item_category {
  border: 2px solid #e4752a;
  padding: 10px 20px;
  border-radius: 10px;
  margin-right: 25px;
  cursor: pointer;
  transition: all 0.33s;
}
.item_category:hover {
  background: #e4752a;
}
.item_category.active {
  background: #e4752a;
}
.filters-cart-mobile {
  display: none;
}
.filter-model {
  top: 0;
  left: 0;
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: white;
  overflow-y: scroll;
}
.Close {
  width: 30px;
  height: 30px;
}
@media screen and (max-width: 1200px) {
  .TopShopPage,
  .filters {
    display: none;
  }
  .filters-cart-mobile {
    display: flex;
    position: fixed;
    right: 75px;
    top: 23px;
  }
  .SearchImg {
    margin-left: 11px;
    top: 117px;
  }
  .top-filter-model {
    padding: 20px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .filter-model.open {
    animation: ModelOpen 0.5s;
    display: block;
  }
  .fulters-mobile {
    padding: 20px 30px;
  }
  .filters-nav,
  .cart {
    margin: 0px 5px;
    cursor: pointer;
  }
}
@media screen and (max-width: 500px) {
  .item_category,
  .Sort {
    font-size: 12px;
  }
  .item_category {
    padding: 5px 10px;
    border: 1px solid #e4752a;
  }
}
/* Окончание категорий и сорта */

.Items {
  width: 100%;
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.Cart {
  background: #eca36b;
  margin: 30px;
  border-radius: 10px;
  width: 300px;
  height: 400px;
}
.top-cart {
  border-radius: 5px 5px 0 0;
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffbe93;
}
.bottom-cart {
  padding: 10px 20px;
}
.text-title {
  font-size: 11px;
}
.bottom-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 40px;
}
.btn-cart {
  border: 0;
  background: #f58244;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: all 0.33s;
}
.btn-cart:hover {
  background-color: #f8691c;
}
/* Окончания создания карточки */
.Overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #929090;
  opacity: 0.33; /* Прозрачность */
  visibility: hidden; /* Изначально скрыто */
  z-index: 1000;
  transition: visibility 0s, opacity 0.7s ease;
}
.Modal-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.Modal {
  transform: translateY(-100%);
  display: flex;
  position: fixed;
  background: #ffd1b2;
  width: 80%;
  height: 80%;
  top: 10%;
  left: 10%;
  border-radius: 10px;
  z-index: 1001;
  visibility: hidden;
  transition: visibility 0s, transform 0.7s ease;
}
.Close-model {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.Modal.open,
.Overlay.open {
  transform: none;
  visibility: visible;
}
.Overlay.open {
  opacity: 0.33;
}
.right-modal {
  background: #ffbe93;
  border-radius: 5px 0 0 5px;
}
.left-modal {
  display: none;
  padding: 20px 30px;
  width: 70%;
}
.left-modal.open {
  display: block;
}
.top-modal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.ModelCart {
  height: 100%;
  width: 100%;
}
.text-model {
  max-width: 530px !important;
  font-size: 20px;
}
.color {
  margin-top: 100px;
}
.item_color {
  margin: 0px 20px;
  margin-left: 0;
  padding: 5px 10px;
  background: #ffbe93;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.33s;
}
.item_color:hover {
  background: #ffbe93;
}
.item_color.active {
  background: #f5ac7b;
}
.price-switch {
  margin-top: 100px;
}

.switch {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5ac7b;
  padding: 10px;
  width: 125px;
  height: 30px;
  border-radius: 5px;
}
.switch a {
  padding: 20px;
  cursor: pointer;
}
.btn-go-cart {
  padding: 15px 35px;
  background: #ffbe93;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.33s;
}
.btn-go-cart:hover {
  background: #f5a976;
}
.bottom-model {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.notification {
  z-index: 10012;
}
@media screen and (max-width: 1000px) {
  .Modal {
    transform: none;
    flex-direction: column;
    left: 0;
    top: 0;
    margin-top: 0%;
    margin-left: 0%;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    transition: none;
  }
  .right-modal {
    width: 100%;
    height: 150%;
  }
  .Close-model {
    position: absolute;
    top: 30px;
    right: 30px;
  }
  .color {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .item_color {
    margin: 10px 0;
  }
}

@media screen and (max-width: 750px) {
  .Cart,
  .text-title {
    font-size: 10px;
  }
  .Cart {
    width: 275px;
    height: 475px;
  }
  .bottom-btn {
    flex-direction: column;
    align-items: flex-start;
  }
  .bottom-model {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}
@media screen and (max-height: 725px) {
  .Model {
    width: 100%;
    height: 100%;
  }
  .color,
  .price-switch {
    margin: 30px 0px;
  }
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}
