.full-Orders {
  margin-top: 50px;
  padding: 0px 30px;
}
.Orders {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
  margin-top: 30px;
  border-radius: 8px;
}
.Order {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}
.left-order {
  padding-left: 30px;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Top-Orders {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.EmptyProduct {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.EmptyProductImg {
  position: absolute;
  opacity: 0.5;
  margin-top: 100px;
}
.bottom-Orders-cart {
  display: flex;
  flex-direction: column;
}
.bottom-Order {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn-ordered {
  padding: 15px 10px;
  text-decoration: none;
  color: rgb(0, 0, 0);
  background: #f5a66a;
  transition: all 0.33s;
  border-radius: 8px;
}
.btn-ordered:hover {
  background: #ffa25b;
}
.RemoveAll {
  cursor: pointer;
  transition: all 0.33s;
}
.RemoveAll:hover {
  color: #fa7373;
}
.cirsle {
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: rgb(207, 205, 205);
  transition: all 0.33s;
}
.switch-order {
  display: flex;
  justify-content: center;
  align-items: center;
}
.count {
  padding: 10px;
}
.switch_item-minus,
.switch_item-plus {
  padding: 5px 10px;
  cursor: pointer;
  transition: all 0.33s;
}
.switch_item-minus:hover,
.switch_item-plus:hover {
  opacity: 1;
}

.cirsle:hover {
  background: rgb(255, 117, 117);
}
.Close-img {
  width: 20px;
  height: 20px;
}
.bottom-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 30%;
}
@media screen and (max-width: 900px) {
  .Orders {
    align-items: initial;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
  }
  .EmptyProduct {
    font-size: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .EmptyProductImg {
    width: 215px;
  }
  .Order {
    display: flex;
    flex-direction: column;
    align-items: initial;
    justify-content: initial;
    width: 200px;
    margin: 20px;
    background: #eca36b;
    border-radius: 8px;
  }
  .right-order {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffbe93;
    border-radius: 8px 8px 0 0;
  }
  .left-order {
    flex-direction: column;
    justify-content: initial;
    align-items: initial;
    padding: 10px;
    font-size: 10px;
    width: 90%;
  }

  .switch-order {
    display: flex;
    justify-content: initial;
    align-items: center;
  }
  .cirsle {
    width: 30px;
    height: 30px;
  }
  .Close-img {
    width: 10px;
    height: 10px;
  }
  .bottom-panel {
    display: flex;
    justify-content: space-between;
    width: initial;
    align-items: center;
  }
}
