@import url('https://fonts.googleapis.com/css2?family=Inria+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Julius+Sans+One&family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

.container {
  max-width: 1300px;
  padding: 0px 30px;
  margin: 0px auto;
}
.Header {
  font-family: 'Julius Sans One', serif;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav_item {
  padding: 0px 20px;
  color: #000000;
  text-decoration: none;
}
.nav_item:hover {
  text-decoration: underline;
}
.btn_nav {
  padding: 10px 30px;
  background: #f58244;
  border-radius: 10px;
  transition: all 0.33s;
}
.btn_nav:hover {
  text-decoration: none;
  background: #e86924;
}
.nav-modile,
.Menu {
  display: none;
}
@media screen and (max-width: 1240px) {
  .nav {
    display: none;
  }
  .nav-modile {
    display: block;
    cursor: pointer;
    position: fixed;
    right: 30px;
  }
}
@media screen and (max-width: 1200px) {
  .Menu {
    display: none;
    top: 0;
    left: 0;
    position: fixed;
    transition: all 2s;
    width: 100%;
    height: 100%;
    animation: ModelOpen 0.5s;
    background-color: white;
    z-index: 2;
    overflow-y: scroll;
  }

  .Menu.open {
    display: block;
  }
  .top-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 30px;
  }
  .nav-mobile-nav-item {
    padding: 10px;
    display: flex;
    flex-direction: column;
  }
  .nav-mobile-nav-item a {
    padding: 10px 20px;
    width: 10%;
  }
  .Close {
    cursor: pointer;
    width: 25px;
    height: 25px;
  }
  @keyframes ModelOpen {
    0% {
      position: fixed;
      transform: translateY(-100%);
    }
  }
}
