.center-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.form-login {
  padding: 30px;
  margin-top: 75px;
  border-radius: 8px;
  width: 33%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-control {
  margin: 20px;
  background-color: #f9d4aa;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  width: 500px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  padding: 25px;
  border-radius: 8px;
}
.title {
  font-size: 28px;
  font-weight: 800;
}
.errors {
  color: rgb(248, 110, 110);
}
.input-field {
  position: relative;
  width: 100%;
}

.input {
  margin-top: 15px;
  width: 100%;
  outline: none;
  border-radius: 8px;
  height: 45px;
  border: 1.5px solid #363636;
  background: transparent;
  padding-left: 10px;
}
.input:focus {
  border: 1.5px solid #de8b10;
}
.input-field .label {
  position: absolute;
  top: 5px;
  font-size: 12px;
  left: 5px;
  color: #303030;
  background-color: #f9d4aa;
  transition: all 0.3s ease;
  pointer-events: none;
  z-index: 1;
  padding-left: 5px;
  padding-right: 5px;
}

.submit-btn {
  margin-top: 30px;
  height: 55px;
  background: #fcb757;
  border-radius: 8px;
  border: 0;
  font-size: 18px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  cursor: pointer;
  transition: all 0.33s;
}

.submit-btn:hover {
  background: #dea049;
}
@media screen and (max-width: 600px) {
  .form-control {
    width: 250px;
  }
}
@media screen and (max-width: 400px) {
  .form-control {
    width: 225px;
  }
}
